import styled from "styled-components";

const ButtonGroup = styled.div`
  border-top: 1px solid #ccc;
  padding-top: 20px;
  margin-top: 20px;

  a, button {
    width: 50%;
    display: inline-block;
  }
`;

export default ButtonGroup;



