import React, { Component } from "react";
import { Experiment, Variant } from "../components/AB"
import ModalDataHandling from "../components/ModalDataHandling"
import ModalTerms from "../components/ModalTerms"
import { trackPageView, track } from "../global/analytics"
import { Original, Simplified } from "../components/Experiments"

const cache = {
  get: (key) => window.localStorage.getItem(key),
  set: (key, value) => window.localStorage.setItem(key, value)
}

class IndexPage extends Component {
  state = { 
    isDataModalOpen: false, 
    isTermsModalOpen: false, 
    provider: null,
  }

  componentDidMount() {
    trackPageView("Landing Page");
  }

  onButtonClick = e => {
    track("Landing Page Action", {
      "Action": "Signup"
    })
  }

  onEnrolment (experimentId, variantId) {
    track("Landing Page Event", {
      "experimentId": experimentId,
      "variantId": variantId,
    })
  }

  handleDataModal = () => {
    this.setState({
      isDataModalOpen: !this.state.isDataModalOpen,
    })
    track("Landing Page Action", {
      "Data Modal": !this.state.isDataModalOpen ? "Opened" : "Closed" 
    })
  }

  handleTermsModal = (provider) => {
    this.setState({
      isTermsModalOpen: !this.state.isTermsModalOpen,
      provider: provider,
    })
    track("Landing Page Action", {
      "Terms Modal": !this.state.isTermsModalOpen ? "Opened" : "Closed",
      provider: provider ? provider : null,
    })
  }

  render() {
    return (
      <div>
        <Experiment id="unroll-landing" onEnrolment={this.onEnrolment} cache={cache}>
          <Variant id={0} name="Original">
            <Original
              onButtonClick={this.onButtonClick}
              handleDataModal={this.handleDataModal}
            />
          </Variant>
          <Variant id={1} name="Original - with direct auth">
            <Simplified
              onButtonClick={this.onButtonClick}
              handleDataModal={this.handleDataModal}
              handleTermsModal={this.handleTermsModal}
            />
          </Variant>
        </Experiment>

        {this.state.isDataModalOpen ? (
          <ModalDataHandling
            isActive={this.state.isDataModalOpen}
            closeModal={this.handleDataModal}
          />
        ) : null}
        {this.state.isTermsModalOpen ? (
          <ModalTerms
            isActive={this.state.isTermsModalOpen}
            closeModal={this.handleTermsModal}
            provider={this.state.provider}
          />
        ) : null}
      </div>
    );
  }
}

export default IndexPage;



